import { Directive, ElementRef } from '@angular/core';

@Directive({
  selector: '[appSticky]'
})
export class StickyDirective implements ISticky {
  public id: string;
  public backgroundColorEnum: number;
  public message: string;
  public zIndex: number;
  public xIndex: number;
  public yIndex: number;
  public width: number;
  public height: number;

  constructor(element: ElementRef) {
    // console.log(element);
  }

}

interface ISticky {
  id: string;
  backgroundColorEnum: number;
  message: string;
  zIndex: number;
  xIndex: number;
  yIndex: number;
  width?: number;
  height?: number;
}
