import { Component, OnInit } from '@angular/core';
import { HttpService } from '../../services/http.service';
import { TakePicService } from '../../services/take-pic/take-pic.service';
import { SaveImageFormComponent } from '../save-image-form/save-image-form.component';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  public httpResponseTest: string;
  public joke: string;

  constructor(
    private http: HttpService,
    private picSrv: TakePicService,
    private dialog: MatDialog) {

  }

  public ngOnInit() {
    this.getJoke();
  }

  public getJoke() {
  }

  public dragEnd(event): void {

  }
  public saveAsImage() {
    const dialogRef = this.dialog.open(SaveImageFormComponent, {
      width: '350px',
      height: '200px'
      // data: {name: this.name, animal: this.animal}
    });
    dialogRef.afterClosed().subscribe(fileName => {
      console.log(fileName);
      // probably should check for malicious names later
      if (fileName !== '' && fileName !== undefined) {
        this.picSrv.addFileName(fileName);
        this.picSrv.addElementName('cheese');
        this.picSrv.takePic();
      } else {
        // maybe something to inform them they did not have a valid entry?
      }
    });
  }
}
