import { Component, OnInit } from '@angular/core';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
@Component({
  selector: 'app-save-image-form',
  templateUrl: './save-image-form.component.html',
  styleUrls: ['./save-image-form.component.css']
})
export class SaveImageFormComponent implements OnInit {

  public fileName = '';

  constructor(public dialogRef: MatDialogRef<SaveImageFormComponent>) { }

  public ngOnInit() {

  }

  public save() {
    this.dialogRef.close(this.fileName);
  }

}
