import { Component, OnInit, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {
  @Output()
  createNew = new EventEmitter();

  @Output()
  newImage = new EventEmitter();

  @Output()
  stylePage = new EventEmitter();

  @Output()
  save = new EventEmitter();

  @Output()
  clearAll = new EventEmitter();

  @Output()
  newImageUpload = new EventEmitter();

  constructor() { }

  ngOnInit() {
  }
  public newStickyClick(): void {
    this.createNew.emit();
  }
  public newImageUploadClick(): void {
    this.newImageUpload.emit();
  }
public newImageClick(): void {
  this.newImage.emit();
}
public stylePageClick(): void {
  this.stylePage.emit();
}
public clearAllClick(): void {
  this.clearAll.emit();
}
public saveClick(): void {
  this.save.emit();
}

}
