import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { Observer } from 'rxjs/Observer';
import * as socketIo from 'socket.io-client';
// const SERVER_URL = 'http://localhost:8080';
const SERVER_URL = 'https://scrum-daddies-backend-development.us-east-1.np.paas.lmig.com';
@Injectable({
  providedIn: 'root'
})
export class WebSocketService {

  private socket;

  public initSocket(): void {
    this.socket = socketIo(SERVER_URL);
  }

  public send(message: any): void {
    this.socket.emit('message', message);
  }

  public onMessage(): Observable<any> {
    return new Observable<any>(observer => {
      this.socket.on('message', (data: any) => observer.next(data));
    });
  }

  public join(clientId: any): void {
    this.socket.emit('join', clientId);
  }

  public onJoin(): Observable<any> {
    return new Observable<any>(observer => {
      this.socket.on('join', (data: any) => observer.next(data));
    });
  }

  public onEvent(event: any): Observable<any> {
    return new Observable<any>(observer => {
      this.socket.on(event, () => observer.next());
    });
  }
}

