import { Injectable } from '@angular/core';
import * as interact from 'interactjs';
import {SandboxComponent} from '../../components/sandbox/sandbox.component';
@Injectable({
  providedIn: 'root'
})
export class InteractService {

  constructor() { }
  /**
   * injects the drag and drop abilities into the given component
   * @param sandbox The component that interactjs will attach to
   */
  public interactInIt(sandbox: SandboxComponent) {
    interact('.draggable')
      .draggable({
        inertia: false,
        restrict: {
          restriction: 'parent',
          endOnly: true,
          elementRect: { top: 0, left: 0, bottom: 1, right: 1 }
        },
        autoScroll: true,
        onmove: (event: interact.InteractEvent) => {
          // sandbox.sendPayload();
          const target = event.target;
          sandbox.whiteboard.stickies
            .filter(sticky => sticky.id === target.id)
            .map(sticky => {
              const x = (parseFloat(sticky.xIndex.toString()) || 0) + event.dx;
              const y = (parseFloat(sticky.yIndex.toString()) || 0) + event.dy;
              sticky.xIndex = x;
              sticky.yIndex = y;
              return sticky;
            });
          sandbox.whiteboard.uploadedImages
            .filter(sticky => sticky.id === target.id)
            .map(sticky => {
              const x = (parseFloat(sticky.xIndex.toString()) || 0) + event.dx;
              const y = (parseFloat(sticky.yIndex.toString()) || 0) + event.dy;
              sticky.xIndex = x;
              sticky.yIndex = y;
              return sticky;
            });
          sandbox.sendPayload();
        },
        onend: function (event) {
        }
      }).resizable({
        edges: { left: false, right: true, bottom: true, top: false },
        onend: function (event) {
        }
      }).on('resizemove', function (event: any) {
        const target = event.target;
        sandbox.whiteboard.stickies
          .filter(sticky => sticky.id === target.id)
          .map(sticky => {
            sticky.width = event.rect.width;
            sticky.height = event.rect.height;
            sandbox.changeRef.detectChanges();
          });

        sandbox.whiteboard.uploadedImages
          .filter(sticky => sticky.id === target.id)
          .map(sticky => {
            sticky.width = event.rect.width;
            sticky.height = event.rect.height;
            sandbox.changeRef.detectChanges();
          });

        sandbox.sendPayload();
      });
  }
}
