import { Component, OnInit, ChangeDetectorRef, ElementRef } from '@angular/core';
import { ISticky } from '../../models/isticky';
import { WhiteBoardModel } from './white-board';
import { TakePicService } from '../../services/take-pic/take-pic.service';
import { MatDialog } from '@angular/material';
import { SaveImageFormComponent } from '../save-image-form/save-image-form.component';
import { EditorComponent } from '../editor/editor.component';
import { ViewImgComponent } from '../view-img/view-img.component';
import { WebSocketService } from '../../services/web-socket.service';
import { HttpService } from '../../services/http.service';
import { Observable } from 'rxjs';
import { InteractService } from '../../services/interact/interact.service';
import { SandboxService } from './sandbox-service';
import { ConfirmComponent } from '../confirm/confirm.component';
let sandbox: SandboxComponent;
@Component({
  selector: 'app-sandbox',
  templateUrl: './sandbox.component.html',
  styleUrls: ['./sandbox.component.css']
})
export class SandboxComponent implements OnInit {

  constructor(
    private picSrv: TakePicService,
    private dialog: MatDialog,
    private socketService: WebSocketService,
    private http: HttpService,
    public changeRef: ChangeDetectorRef,
    public interactSrv: InteractService,
  ) {
    sandbox = this;
    // this.
    // sandboxService.setComponent(this);
  }

  public whiteboard = new WhiteBoardModel();
  public sandboxService: SandboxService = new SandboxService(this);
  public ioConnection: any;
  public backgroundTemplateENUM = WhiteBoardModel.TemplateStartingENUM;
  public templates = WhiteBoardModel.templates;
  public stickyColors: Array<String> = WhiteBoardModel.stickyColors;
  public editorConfig: any = WhiteBoardModel.editorConfig;
  public clientId: string = this.sandboxService.guid();
  public mouseDown = 0;

  public ngOnInit() {
    this.interactSrv.interactInIt(sandbox);
    // this.getBoard();
    this.initIoConnection();
    this.trackMouseHold();
  }

  private trackMouseHold(): void {
    this.sandboxService.trackMouseHold();
  }

  private initIoConnection(): void {
    this.socketService.initSocket();
    this.ioConnection = this.socketService.onMessage()
      .subscribe(payload => {
        if (payload && payload.message.clientId !== this.clientId && this.mouseDown === 0) {
          this.whiteboard.stickies = payload.message.stickies;
          this.whiteboard.uploadedImages = payload.message.stickyPics;
          this.changeRef.detectChanges();
        }
      });
    this.socketService.join(this.clientId);
    this.socketService.onJoin().subscribe(payload => {
      if (payload.clientId === this.clientId) {
        this.whiteboard.stickies = payload.stickies;
        this.whiteboard.uploadedImages = payload.stickyPics;
      }
      console.log('connection made');
    });
    this.socketService.onEvent('connected').subscribe(() => console.log('connected'));
    this.socketService.onEvent('disconnected').subscribe(() => console.log('disconnected'));
  }

  public sendMessage(message: any) {
    this.socketService.send({ message: message });
  }

  public viewImage(img): void {
    this.dialog.open(ViewImgComponent, {
      data: img.message
    });
  }

  public deleteImage(img): void {
    this.whiteboard.uploadedImages.forEach((x, index) => {
      if (x === img) {
        this.whiteboard.uploadedImages.splice(index, 1);
        this.sendPayload();
        this.sendPayload();
      }
    });
  }
  public sendPayload(): void {
    const payload = {
      clientId: this.clientId,
      stickies: this.whiteboard.stickies,
      stickyPics: this.whiteboard.uploadedImages
    };
    this.sendMessage(payload);
  }

  public deleteSticky(sticky): void {
    this.sandboxService.deleteSticky(sticky);
  }

  public newSticky(): void {
    const dialogRef = this.dialog.open(EditorComponent, {});
    dialogRef.afterClosed().subscribe(info => {
      this.sandboxService.addNewSticky(info);
    });
  }

  public findZIndex(sticky: ISticky): void {
    this.sandboxService.findZIndex(sticky);
  }
  public newImage(): void {
    const dialogRef = this.dialog.open(SaveImageFormComponent, {
      width: '350px',
      height: '200px'
    });
    dialogRef.afterClosed().subscribe(fileName => {
      // probably should check for malicious names later
      if (fileName !== '' && fileName !== undefined) {
        this.picSrv.addFileName(fileName);
        this.picSrv.addElementName('sandbox');
        this.picSrv.takePic();
      } else {
        // maybe something to inform them they did not have a valid entry?
      }
    });
  }

  public editSticky(sticky): void {
    const dialogRef = this.dialog.open(EditorComponent, {data: {sticky: sticky}});
    dialogRef.afterClosed().subscribe(info => {
      if (info) {
        this.whiteboard.stickies.forEach((x, index) => {
          if (x === sticky) {
            this.whiteboard.stickies[index].message = info;
          }
        });
      }
      this.sendPayload();
      this.sendPayload();
    });
  }

  public changeTemplate(): void {
    this.sandboxService.changeTemplate();
  }

  public clearPage(): void {
    const dialogRef = this.dialog.open(ConfirmComponent, {});
    dialogRef.afterClosed().subscribe(closeIt => {
      if (closeIt) {
        this.sandboxService.clearPage();
      }

    });
  }

  public imageUpload(file): void {
    this.sandboxService.imageUpload(file);
  }

  public onFileChanged(event): void {
    this.sandboxService.onFileChanged(event);
  }
  public changeStickyColor(sticky): void {
    this.sandboxService.changeStickyColor(sticky);
  }
}
